body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 75px;
}
button{
    font-size:20px;
}

button.clockwise {
    margin: 10px 5px 10px 5px;
}
ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
}

.game-info {
    margin-left: 20px;
}
.game-board>div{
    border: 5px solid black;
    position:relative;
}

.rotate-t{
    top: -25px;

}

.rotate-r{
    right:-50px;
}

.rotate-l{

    left: -25px;
}

.rotate-b{

    bottom:-50px;
}